import React from 'react'

export default function List({items, removeItem, editItem}) {
    return (
        <div className="grocery-list">
            {items.map((item)=>{
                return(
                    <li key={item.id} className="grocery-item">
                        <p className="title">{item.title}</p>
                        <div className="btn-container">
                            <button className="edit-btn" type="button" onClick= {() => editItem(item.id)}>Edit</button>
                            <button className="delete-btn" type="button" onClick={()=> removeItem(item.id)}>Delete</button>
                        </div>
                    </li>

                )

            })}
        </div>
    )
}
